@use "global" as *;

.p-media {
  padding-left: $padding-sp;
  padding-right: $padding-sp;
  @include mq("md") {
    display: flex;
    padding-left: initial;
    padding-right: initial;
  }
}

.p-media__img-wrapper {
  @include mq("md"){
    flex: 0 1 54.6875%;
  }
}

.p-media__img {
  width: 100%;
}

.p-media__body {
  @include mq("md"){
    margin-left: 7.1354167vw;
  }
}
.p-media__title {
  margin-top: rem(32);
  font-size: rem(36);
  font-weight: $medium;
  line-height: 1.7;
}

.p-media__text {
  margin-top: rem(75);
  line-height: 2;
}

.p-media__button {
  margin-top: rem(75);
}

.p-media--row_reverse {
  flex-direction: row-reverse;
}

.p-media__body--row_reverse {
  margin-right: 7.1354167vw;
}
